import React from 'react';
import ReactAutocomplete from 'react-autocomplete';
 
export default class TagAutoComplete extends React.Component {
		  constructor (props) {
		    super(props)
		    this.users = [];
		    var getUsersUrl = require('./api_url.js').getUsersURL;
		    var that = this;
		    require('./CommentFunctions.js').getAllUsers(getUsersUrl, this, function() {
		    	that.setState({dummy : ""})
		    });
		    this.selectedUserId = "";
		    this.state = {
		      value: '',
		    }
		  }

		  fetchUserComments = () => {
			  this.props.fetchUserComments(this.selectedUserId);
		  };
		  
		  ifUserExists = () =>{
			 var userName = this.state.value;
			 var found = false;
			 var that = this;
			 this.users.map(function(user){
				 if(user.userName === userName){
					 found = true;
					 that.selectedUserId = user.userId;
					 return true;
				 }
			 })
			 return found;
		  }
		  
		  render() {
		    return (
		    		<div>
		    		<h4 class="text-success">Timelines <span class="glyphicon glyphicon-hourglass"></span> &nbsp;&nbsp;<span class="glyphicon glyphicon-search"></span></h4>
		      <ReactAutocomplete
		        items={this.users}
		    		shouldItemRender={(item, value) => item.userName.toLowerCase().indexOf(value.toLowerCase()) > -1}
			        getItemValue={item => item.userName}
			        renderItem={(item, highlighted) =>
			          <div
			          	key={item.userName}
			            style={{ backgroundColor: highlighted ? '#eee' : 'transparent'}}
			          >
			            {item.userName}
			          </div>
			        }
			        value={this.state.value}
			        onChange={e => this.setState({ value: e.target.value })}
		      	    onSelect={value => this.setState({ value })}
			      />
			    {(this.state.value !== "") && (this.ifUserExists()) &&
		      		<div><br/><mark>See <a href="#" onClick={this.fetchUserComments.bind(this)}>{this.state.value}'s </a>Timeline</mark></div>
			      }
		      </div>
		    )
		    
		  }
		
}


